<template>
	<div class="BxContentProf__PersonalInfo">
		<div class="boxNotif" v-if="CheckAdditional.message !== null">
         <v-snackbar
				v-model="snackbar"
				:color="NotifColor"
				:timeout="timeout"
				top
			>
					<p>{{NotifText}}</p>
				<!-- <v-btn dark flat @click="snackbar = false">
					Close
				</v-btn> -->
			</v-snackbar>
		</div>

		<div class="FormTitle">
			<p>Personal Informations</p>
		</div>
		<v-form ref="fmPersonalInfo" enctype="multipart/form-data" class="BxContentProf__PersonalInfo__bxfmPersonalInfo" v-model="valid" lazy-validation>
			<div class="FormGroup">
				<v-label>Full Name</v-label>
				<v-text-field required :rules="filedRules" v-model='fullname' ></v-text-field>
			</div>

			<div class="FormGroup">
				<v-label>Nick Name</v-label>
				<v-text-field required :rules="filedRules" v-model='nickname' ></v-text-field>
			</div>

			<div class="FormGroup">
				<v-label>Birthdate</v-label>
				<v-menu v-model="menu1" full-width max-width="290">
					<v-text-field
					slot="activator"
					v-model="DateBirthFormated"
					readonly
					></v-text-field>
					<v-date-picker
					v-model="date"
					@change="menu1 = false"
					></v-date-picker>
				</v-menu>
			</div>
			<div class="FormGroup notAll">
				<v-label>Username</v-label>
				<v-text-field required :rules="filedRules" readonly disabled v-model='username'></v-text-field>
			</div>

			<div class="FormGroup">
				<v-label>Phone</v-label>
				<v-text-field required :rules="filedRules" v-model='phone'></v-text-field>
			</div>

			<div class="FormGroup">
				<v-label>Gender</v-label>
				<v-select class="text-capitalize" :items="items" required :rules="filedRules" v-model="gender"></v-select>
			</div>

			<div class="FormGroup">
				<v-label>Country</v-label>
				<multiselect
					v-model="country"
					:options="listofcountry"
					label="name"
					track-by="name"
					placeholder="Select Country"
				>
				</multiselect>
			</div>

			<div class="FormGroup">
				<v-label>Spoken Language</v-label>
				<multiselect
					v-model="spokenLanguage"
					:options="listoflanguage"
					label="name"
					track-by="name"
					placeholder="Select Language"
				>
				</multiselect>
			</div>

			<div class="FormGroup" style="width:100%">
				<v-label>Short Bio</v-label>
				<v-textarea
					row-height=12
					solo
					v-model="bio"
					style="margin-top:20px;"
				></v-textarea>
			</div>

		</v-form>
		<span class="line"></span>

		<div class="FormGroupBtn">
			<v-btn  class="btnRoundNeo " :disabled="!valid" @click="UpdatePersonalInfo">Save Personal Info</v-btn>
		</div>

		 <v-dialog v-model="dialogLoding" width="150" persistent content-class="PersonalLoading">
         <v-card>
            <v-card-text class="text-xs-center">
               <v-progress-circular
                  indeterminate
                  color="primary"
                  :size="70"
                  :width="7"
               ></v-progress-circular>
            </v-card-text>
         </v-card>
      </v-dialog>

	</div>
</template>

<script>
	import axios from 'axios'
	import ListofLanguage from '../../../staticjson/ListofLanuage.json';
	import ListofCountry from '../../../staticjson/ListofCountry.json';
	import moment from 'moment'
	import Multiselect from 'vue-multiselect'

	export default{
		props:[
			'Additional'
		],
		components: {
			Multiselect
		},
		data () {
			return {
				dialogLoding: false,
				snackbar: false,
            NotifColor:'',
            NotifText:'',
            timeout: 3000,
				listoflanguage :ListofLanguage,
				valid: false,
				filedRules: [
					v => !!v || 'Field is required',
				],
				items: ['male', 'female'],
				date: new Date().toISOString().substr(0, 10),
				menu1: false,
				username: this.Additional.username,
				fullname: this.Additional.fullname,
				nickname: this.Additional.nickname,
				gender: this.Additional.gender,
				birthday: this.Additional.date_of_birth ,
				listofcountry:ListofCountry,
				setCountry: null,
				country: { name: this.Additional.country },
         	state: this.Additional.state,
				address: this.Additional.address,
				city: this.Additional.city,
				phone: this.Additional.phone,
				hobbies: this.Additional.student_hobby,
         	likes: this.Additional.student_like,
         	dislike: this.Additional.student_dislike,
				certifications: this.Additional.cert_studying,
				zip: this.Additional.zip,
				spokenLanguage: { name: this.Additional.spoken_language },
				userLanguage: this.Additional.user_language,
				bio: this.Additional.short_bio
			}
		},
		computed: {
    		DateBirthFormated () {
      		return this.date ? moment(this.date).format('dddd , MMMM Do  -  YYYY') : ''
			},
			CheckAdditional(){
            return this.$store.state.DataAdditional
			},
			CckLoadAvatar(){
				return this.$store.state.CckLoadAvatar
			},
			CckLoadDataPrsinfo(){
				return this.$store.state.CckLoadDataPrsinfo
			}
		},
		methods: {
			UpdatePersonalInfo () {
				if (this.$refs.fmPersonalInfo.validate()) {
					const formData = new FormData()
					formData.append('fullname', this.fullname)
					formData.append('nickname', this.nickname)
					formData.append('date_of_birth', this.date)
					formData.append('phone', this.phone)
					formData.append('gender', this.gender)
					formData.append('country', this.country.name)
					formData.append('city', this.city)
					formData.append('state', this.state)
					formData.append('address', this.address)
					formData.append('zip', this.zip)
					formData.append('student_like', this.likes)
					formData.append('student_dislike', this.dislike)
					formData.append('short_bio', this.bio)
					formData.append('student_hobby', this.hobbies)
					formData.append('user_language', this.userLanguage)
					formData.append('spoken_language', this.spokenLanguage.name)
					formData.append('_method', 'PATCH')

					console.log(formData)

					this.$store.dispatch('POST_UPDATE_PERSONAL_INFO',formData)
				}
				this.dialogLoding = true
			},
		},
		mounted() {
			//SET VALUE Birthdate
			const TheBirthdate = new Date(this.birthday).toISOString().substr(0, 10)
			this.date = TheBirthdate
		},
		watch: {
			CckLoadAvatar(val){
				if (!val) return

				if(val.status === 200){
					this.$store.dispatch("GET_PROFILEADDITIONAL")
					this.NotifColor='success'
					this.NotifText= 'Update Avatar is Successfully'
					this.snackbar = true
				}else if(val.status === 500){
					this.NotifColor='error'
					this.NotifText= 'Please Try Again Later'
					this.snackbar = true
				}
			},
			CckLoadDataPrsinfo(val){
				if (!val) return

				if (val.status === 200){
					this.dialogLoding = false
					this.$store.dispatch("GET_PROFILEADDITIONAL")
					this.NotifColor='success'
					this.NotifText = 'Update Personal is Successfully'
					this.snackbar = true
				}
			}
		}
	}
</script>



